html {
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-family: "Avenir Next Cyr", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: normal;
  color: #111111;
  background-color: #F7F9FA;
  display: flex;
  flex-direction: column;
}

body #shadow {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 57;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

body #shadow.active {
  visibility: visible;
  opacity: 1;
}

body .hide {
  display: none !important;
}

body.open-mobile-drop #mobile-drop {
  left: 0;
}

body.open-filter #filter {
  left: 0;
}

html,
body {
  height: 100%;
