  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  margin: 0 0 15px;
}

.sitemap li {
  list-style: none;
  padding: 0 0 0 20px;
  line-height: 1;
  margin: 0 0 8px;
}

.sitemap a {
  font-size: 14px;
  color: #111111;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  line-height: 1;
  transition: all .3s;
}

.sitemap a:hover {
  color: #2A6AF0;
  border-bottom-color: #2A6AF0;
}

.sitemap__item {
  display: inline;
  min-width: 230px;
}

.sitemap__item > ul > li {
  margin: 0 0 12px;
}

.sitemap__item > ul > li > a {
  font-weight: 700;
  display: inline-block;
}

.sitemap__item > ul > li ul {
  margin: 8px 0 32px;
}

.slider {
  position: relative;
  margin: 32px 0 37px;
}

.slider__image:nth-child(n+2) {
  display: none;
}

.slider .slick-prev, .slider .slick-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0;
  border: none;
  z-index: 1;
  cursor: pointer;
  background: transparent;
  width: 16px;
  height: 27px;
  padding: 0;
}

.slider .slick-prev:before, .slider .slick-next:before {
  content: '';
  width: 16px;
  height: 27px;
  position: absolute;
  background: url(../images/icons/arrow.svg) no-repeat;
  background-size: cover;
  top: 0;
  left: 0;
}

.slider .slick-prev.slick-disabled, .slider .slick-next.slick-disabled {
  opacity: .5;
}

.slider .slick-prev {
  left: -24px;
  transform: translateY(-50%) rotate(-180deg);
}

.slider .slick-next {
  right: -24px;
}

.slider .slick-dots {
  width: auto;
  height: 8px;
  text-align: left;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: calc(100% + 12px);
}

.slider .slick-dots li {
  display: flex;
  margin: 0 12px 0 0;
}

.slider .slick-dots li:last-child {
  margin-right: 0;
}

.slider .slick-dots li.slick-active button {
  background: #2A6AF0;
}

.slider .slick-dots button {
  border: none;
  position: relative;
  width: 8px;
  height: 8px;
  padding: 0;
  border-radius: 50%;
  outline: none;
  background: #C2D5FC;
  overflow: hidden;
  cursor: pointer;
  transition: all .3s;
  text-indent: -200em;
}

.slider .slick-dots button:hover {
  background: #2A6AF0;
}

@media (min-width: 1080px) {
  .slider {
    margin: 32px 28px 37px;
  }
  .slider .slick-prev {
    left: -28px;
  }
  .slider .slick-next {
    right: -28px;
  }
}

.text {
  background: #fff;
  border-radius: 0 0 14px 14px;
  padding: 0 24px 32px;
  font-size: 14px;
  line-height: 23px;
  font-weight: 500;
}

.text h1 {
  font-weight: 600;
  line-height: 1;
}

.text h2 {
  font-weight: 600;
  font-size: 24px;
  line-height: 24px;
  margin: 32px 0 0;
}

.text p {
  font-size: inherit;
  line-height: inherit;
  color: #98A0B0;
}

.text > ul {
  position: relative;
  font-size: inherit;
  line-height: inherit;
  color: #98A0B0;
  margin: 16px 0 32px;
}

.text > ul li {
  position: relative;
  padding: 0 0 0 12px;
  margin: 0 0 4px;
}

.text > ul li:before {
  position: absolute;
  content: '';
  width: 6px;
  height: 6px;
  left: 0;
  top: 10px;
  background: #2A6AF0;
  border-radius: 50%;
}

.text ol {
  position: relative;
  list-style-type: none;
  counter-reset: num;
  font-size: inherit;
  line-height: inherit;
  color: #98A0B0;
  margin: 16px 0 32px;
}

.text ol li {
  position: relative;
  padding: 0 0 0 17px;
  margin: 0 0 4px;
}

.text ol li:before {
  position: absolute;
  content: counter(num) ".";
  counter-increment: num;
  left: 0;
  top: 0;
  line-height: 26px;
  color: #2A6AF0;
  font-weight: 700;
  font-size: 16px;
}

.text blockquote {
  padding: 48px 24px 20px;
  margin: 48px 0 0;
  position: relative;
  font-weight: 500;
  background: #EEF3FC;
  font-size: inherit;
  line-height: inherit;
  color: #98A0B0;
  border-radius: 14px;
}

.text blockquote:before {
  position: absolute;
  content: '';
  width: 64px;
  height: 64px;
  left: 24px;
  top: -24px;
  background: url(../images/icons/quote.png) no-repeat;
}

.text blockquote cite {
  display: block;
  text-align: right;
  font-size: 18px;
  color: #111111;
  line-height: 26px;
  margin: 16px 0 0;
}

@media (min-width: 1080px) {
  .text {
    font-size: 16px;
    line-height: 26px;
  }
}

.up {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 15px;
  z-index: 55;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  transition: bottom .3s;
  background: #2A6AF0;
}

.up > svg {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
}

.up.absolute {
  position: absolute;
  top: -24px;
}
